// pages/index.tsx
import { useEffect } from 'react';
import { useRouter } from 'next/router';

export default function Home() {
  const router = useRouter();

  useEffect(() => {
    // 当页面加载时，重定向到登录页面
    router.push('/app/auth/signin');
  }, [router]);

  // 可以渲染一个加载指示器或其他内容，直到重定向发生
  return (
    <div>
      <h1>重定向中...</h1>
    </div>
  );
} 
